/**/

#loader-wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: #ECF0F1;
    /* display: none; */
}

.wave {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    right: 0;
    left: 0;
    top: 26vh;
}

.wave div {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 6.5px;
    /*background: #25c1ac;*/
    -webkit-animation: wave 2.6s cubic-bezier(0.626, 0, 0, 1) infinite;
            animation: wave 2.6s cubic-bezier(0.626, 0, 0, 1) infinite;
    margin-right: 13px;
}

.wave div:nth-child(1) {
    -webkit-animation-delay: -0.34667s;
            animation-delay: -0.34667s;
    background: url('/punkapireact/images/preloderimg.png') no-repeat;
    height: 301px;
    background-size: contain;
    width: 100px;
}

.wave div:nth-child(2) {
    -webkit-animation-delay: -0.26s;
            animation-delay: -0.26s;
    background: url('/punkapireact/images/preloderimg.png') no-repeat;
    height: 301px;
    background-size: contain;
    width: 100px;
}

.wave div:nth-child(3) {
    -webkit-animation-delay: -0.17333s;
            animation-delay: -0.17333s;
    height: 301px;
    background-size: contain;
    width: 100px;
    background: url('/punkapireact/images/preloderimg.png') no-repeat;
}

.wave div:nth-child(4) {
    -webkit-animation-delay: -0.08667s;
            animation-delay: -0.08667s;
    height: 301px;
    background-size: contain;
    width: 100px;
    background: url('/punkapireact/images/preloderimg.png') no-repeat;
}

@-webkit-keyframes wave {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    40% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    60% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    80% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes wave {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    40% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    60% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    80% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

